<template>
  <div class="detail-target">
    <div class="common-flex">
    <div class="btnGroup">
      <div :style="{backgroundColor: tab === 1 ?'#0084FF':'#000',color:(tab === 1 ?'#fff':'rgba(255,255,255,0.6)')}" @click="changeTab(1)">经济指标</div>
      <div :style="{backgroundColor: tab === 2 ?'#0084FF':'#000',color:(tab === 2 ?'#fff':'rgba(255,255,255,0.6)')}" @click="changeTab(2)">性能指标</div>
      <div :style="{backgroundColor: tab === 3 ?'#0084FF':'#000',color:(tab === 3 ?'#fff':'rgba(255,255,255,0.6)')}" @click="changeTab(3)">可靠性指标</div>
    </div>
    </div>
    <div class="card-group" >
      <div class="card"
          v-for="(item, index) in chartList"
          :key="index">
        <div class="title">{{item.title}}</div>
        <div class="chart-container">
          <custom-chart :data="item.value"
                        v-bind="item"
                        :id="index"
                        :height="280"
                        ></custom-chart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import customChart from '@/components/common-chart/common-chart'
import { getAnalysis } from '@/api/device'
export default {
  name: 'detail-target',
  components: {
    customChart
  },
  data () {
    return {
      tab: 1,
      chartList: [],
      deviceId: 0,
      deviceList: []
    }
  },
  mounted () {
    this.changeTab(1)
  },
  methods: {
    changeTab (index) {
      this.tab = index
      this.init()
      setTimeout(() => {
        this.changeData(index)
      })
    },
    init () {
      this.chartList = []
    },
    changeData (index) {
      this.getDatas(index)
    },
    getDatas (index) {
      let { deviceId } = this
      let fieldType = this.getType(index)
      getAnalysis({deviceId, fieldType}).then(res => {
        if (res.code === 200) {
          this.chartList = res.data.indexs.map(i => {
            console.log(i.value)
            return {
              title: i.title,
              type: 'line',
              xAxis: {
                key: 'x'
              },
              yAxis: {
                symbol: i.unit
              },
              legend: i.legend.map((l, idx) => {
                return {
                  ...l,
                  color: this.getColor(idx)
                }
              }),
              value: i.value
            }
          })
        } else {
          this.chartList = []
          this.$Message.warning(res.msg)
        }
      })
    },
    getColor (index) {
      switch (index) {
        case 0:
          return '#0084FF'
        case 1:
          return '#1CC96C'
        case 2:
          return '#b6a2de'
        case 3:
          return '#ffb980'
      }
    },
    getType (index) {
      switch (index) {
        case 1:
          return 'ECONOMY'
        case 2:
          return 'PERFORMANCE'
        case 3:
          return 'RELIABILITY'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.common-flex{
  display: flex;
  justify-content: center;
}
.detail-target {
  width: 100%;
  height: 100%;
  padding: 24px;
  overflow: hidden;
  .card-group{
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    .card {
      width: 810px;
      margin-right: 12px;
      margin-bottom: 24px;
      &:nth-child(4n) {
        margin-right: 0;
      }
      .title {
        margin-bottom: 12px;
        // color: rgba(0, 0, 0, 0.8);
        color: rgba(255, 255, 255, 0.8);
        font-size: 14px;
        font-family: Alibaba PuHuiTi;
        font-weight: 400;
      }
      .chart-container {
        width:810px;
        height: 280px;
        padding: 11px 14px;
        background: #19181D;
        border: 1px solid rgba(0, 0, 0, 0.050980392156862744);
        border-radius: 8px;
      }
    }
  }
}
.btnGroup{
  display: flex;
  align-items: center;
  margin: 12px auto 24px;
  justify-content: end;
  border-radius: 4px;
  margin-right: 16px;
  div{
    margin-right: 1px;
    cursor: pointer;
    padding: 6px 12px;
    // border: 1px solid #000;
  }
}
</style>



