<template>
  <div class="single-device">
    <div class="left">
      <div class="left-head border">平台简介</div>
      <div class="left-device border">
        <div class="item" v-for="item in deviceList" :key="item.id">
          <div class="item-left" :style="{ backgroundColor: item.bg }">
            <div
              :class="['iconfont', item.icon]"
              :style="{ color: item.color }"
            ></div>
          </div>
          <div class="item-right">
            <div class="name">{{ item.name }}</div>
            <div class="data">
              <span>{{ item.value }}</span>
              <span>/{{ item.unit }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="left-enivronment border">
        <!-- <div class="title">环境概况(温度+湿度+PM2.5)</div> -->
        <div class="title" @mouseenter="ChangeShow(1)" v-if="showTitle">
          {{ getName(chartId) }}
        </div>
        <div class="title" @mouseleave="ChangeShow(2)" v-if="!showTitle">
          <Select v-model="chartId" @on-change="changeChart">
            <Option
              v-for="item in chartOptions"
              :key="item.id"
              :value="item.id"
            >
              {{ item.name }}
            </Option>
          </Select>
        </div>
        <div class="chart" id="chart" style="width: 100%;height:200px;"></div>
      </div>
      <div class="left-danger">
        <div class="title">故障告警</div>
        <div class="left-dangertable">
          <Table
            class="m-table no-scroll no-border"
            stripe
            :loading="loading.dangerTable"
            :columns="dangerTableColumns"
            highlight-row
            height="200"
            :data="dangerTableData"
          ></Table>
        </div>
      </div>
    </div>
    <div class="right">
      <div :class="['right-monitor', isFull ? 'active' : '']">
        <div class="right-head">
          <div class="right-head-left">实时监测</div>
          <div class="right-head-right">
            <!-- <div class="jump">
              <Button
                type="default"
                @click="jump"
                custom-icon="iconfont icon-chazhao"
                >查找</Button
              >
            </div> -->
            <div class="full-screen">
              <Button
                type="default"
                @click="fullSreen"
                custom-icon="iconfont icon-tiaozhuanmianban"
                >{{ isFull ? "退出全屏" : "全屏" }}</Button
              >
            </div>
          </div>
        </div>
        <div class="right-screen" id="map">
          <div class="modal" v-if="showModal">
            <Icon
              type="ios-loading"
              style="animation: ani-demo-spin 1s linear infinite;"
            />
          </div>
        </div>
      </div>
      <div class="right-target">
        <div class="right-target-item right-target-financial">
          <div class="label">经济指标</div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in financialList"
              :key="item.id"
            >
              <div>{{ item.name }}</div>
              <div class="value">
                <span class="num">{{ item.num }}</span>
                <span class="symbol">/{{ item.symbol }}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="right-target-item right-target-performence">
          <div class="label">性能指标</div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in performenceList"
              :key="item.id"
            >
              <div class="icon" :style="{ backgroundColor: item.bg }">
                <span
                  :class="['iconfont', item.icon]"
                  :style="{ color: item.color }"
                ></span>
              </div>
              <div class="data">
                <div>{{ item.name }}</div>
                <div class="value">
                  <span class="num">{{ item.num }}</span>
                  <span class="symbol">/%</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="right-target-item right-target-reliable">
          <div class="label">可靠性指标</div>
          <div class="content">
            <div
              class="content-item"
              v-for="item in reliableList"
              :key="item.id"
            >
              <div class="icon" :style="{ backgroundColor: item.bg }">
                <span
                  :class="['iconfont', item.icon]"
                  :style="{ color: item.color }"
                ></span>
              </div>
              <div class="data">
                <div>{{ item.name }}</div>
                <div class="value">
                  <span class="num">{{ item.num }}</span>
                  <span class="symbol">{{ item.symbol }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Select } from 'iview'
import {
  getChartSingle,
  getAddress,
  getPlatformStatistics,
  getChartType,
  changeChartType,
  getDeviceInfo,
  getWarningInfo
} from '@/api/device'

export default {
  name: 'single-device',
  components: {
    Select
  },
  data () {
    return {
      deviceList: [
        {
          id: 1,
          name: '设备总数',
          icon: 'icon-shebei1',
          type: 'device_total',
          value: '',
          unit: '台',
          bg: 'rgba(0, 132, 255, .2)',
          color: 'rgba(0, 132, 255, 1)'
        },
        {
          id: 2,
          name: '运维人员总数',
          icon: 'icon-renyuan',
          unit: '人',
          type: 'maintenance_total',
          value: '',
          color: 'rgba(0, 195, 225, 1)',
          bg: 'rgba(0, 195, 225, .2)'
        },
        {
          id: 3,
          name: '开机数量',
          icon: 'icon-a-kaiji1',
          unit: '台',
          type: 'boot_up_total',
          value: '',
          bg: 'rgba(28, 201, 108, .2)',
          color: 'rgba(28, 201, 108, 1)'
        },
        {
          id: 4,
          name: '发电数量',
          icon: 'icon-fadianji',
          unit: '台',
          type: 'power_generation_total',
          value: '',
          bg: 'rgba(26, 62, 245, .2)',
          color: 'rgba(26, 62, 245, 0.97)'
        },
        {
          id: 5,
          name: '故障数量',
          icon: 'icon-a-guzhang2',
          unit: '个',
          type: 'fault_total',
          value: '',
          bg: 'rgba(241, 33, 33, .2)',
          color: 'rgba(241, 33, 33, 1)'
        }
      ],
      search: {
        location: '',
        status: ''
      },
      deviceTableColumns: [
        {
          title: '型号',
          key: 'device',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '子系统型号',
          key: 'subDevice',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '联系方式',
          key: 'content',
          align: 'left',
          ellipsis: true,
          tooltip: true
        }
      ],
      dangerTableColumns: [
        {
          title: '设备名称',
          key: 'deviceName',
          align: 'left'
        },
        {
          title: '告警信息',
          key: 'errorDesc',
          align: 'left',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '告警时间',
          key: 'alertTime',
          align: 'left',
          ellipsis: true,
          tooltip: true
        }
      ],
      dangerTableData: [],
      screen: '',
      loading: {
        deviceTable: false,
        dangerTable: false
      },
      deviceCurrentIndex: 0,
      dangerCurrentIndex: 0,
      financialList: [
        {
          id: 1,
          name: '累计总发电',
          type: 'POWER_TOTAL',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 2,
          name: '当日总发电',
          type: 'POWER_DAILY',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 3,
          name: '累计上网电',
          type: 'ONLINE_POWER_TOTAL',
          num: '130',
          symbol: 'kWh'
        },
        {
          id: 4,
          name: '碳减排',
          type: 'CARBON_REDUCE_TOTAL',
          num: '130',
          symbol: 't'
        },
        {
          id: 5,
          name: '累计水耗',
          type: 'WATER_CONSUMPTION_TOTAL',
          num: '130',
          symbol: 'kg'
        },
        {
          id: 6,
          name: '累计气耗',
          type: 'GAS_CONSUMPTION_TOTAL',
          num: '130',
          symbol: 'm³'
        }
      ],
      performenceList: [
        {
          id: 1,
          name: '历史最高效率',
          num: '96',
          icon: 'icon-zuigao',
          bg: 'rgb(23, 80, 236, .2)',
          type: 'HISTORY_MAX_POWER_EFFICIENCY',
          color: '#1750EC'
        },
        {
          id: 2,
          name: '历史最大效率',
          num: '96',
          icon: 'icon-zuida',
          bg: 'rgba(0, 132, 255, .2)',
          type: 'HISTORY_MAX_POWER_EFFICIENCY',
          color: '#0084FF'
        },
        {
          id: 3,
          name: '历史平均效率',
          num: '96',
          icon: 'icon-pingjun',
          bg: 'rgba(36, 204, 53, .2)',
          type: 'HISTORY_MAX_AVG_POWER_EFFICIENCY',
          color: '#24CC35'
        }
      ],
      reliableList: [
        {
          id: 1,
          name: '发电时长',
          num: '96',
          symbol: '/h',
          icon: 'icon-a-guzhang2',
          bg: 'rgba(241, 33, 33, .2)',
          type: 'RUNTIME_TOTAL',
          color: '#F12121'
        },
        {
          id: 2,
          name: '平均故障次数',
          num: '96',
          symbol: '/次',
          icon: 'icon-a-lianhe62',
          bg: 'rgba(250, 197, 22, .2)',
          type: 'DOWN_NUM_AVG_DAILY',
          color: '#FAC516'
        },
        {
          id: 3,
          name: '累计故障次数',
          num: '96',
          symbol: '/次',
          icon: 'icon-shichang',
          bg: 'rgba(255, 118, 0, .2)',
          type: 'FAIL_NUM_TOTAL',
          color: '#FF7600'
        }
      ],
      markerList: [[119.209927, 36.725914]],
      isFull: false,
      showModal: true,
      location: [119.209927, 36.725914],
      map: {},
      chartId: 'POWER_GENERATION_AMOUNT',
      chartOptions: [
        {
          id: 'POWER_GENERATION_AMOUNT',
          name: '发电量'
        },
        {
          id: 'CARBON_EMISSIONS',
          name: '碳减排'
        },
        {
          id: 'WATER_CONSUMPTION',
          name: '水耗'
        },
        {
          id: 'GAS_CONSUMPTION',
          name: '气耗'
        },
        {
          id: 'POWER_GENERATION_EFFICIENCY',
          name: '发电效率'
        },
        {
          id: 'POWER_GENERATION',
          name: '发电功率'
        },
        {
          id: 'RUN_TIME',
          name: '运行时长'
        },
        {
          id: 'FAIL_NUM',
          name: '故障次数'
        },
        {
          id: 'TROUBLE_FREE_OPERATION_TIME',
          name: '无故障运行时长'
        },
        {
          id: 'DOWN_TIME',
          name: '停机次数'
        }
      ],
      showTitle: true,
      deviceId: 0
    }
  },
  mounted () {
    this.getChartType()
    this.getPlatformStatistics()
    this.initMap()
    this.getDeviceInfo()
  },
  methods: {
    getDeviceInfo () {
      getDeviceInfo({ id: 0 }).then(res => {
        if (res.code === 200) {
          let info = res.data
          for (let item of this.financialList) {
            item.num = info.economyIndex[item.type]
          }
          for (let item of this.performenceList) {
            item.num = info.performanceIndex[item.type]
          }
          for (let item of this.reliableList) {
            item.num = info.reliabilityIndex[item.type]
          }
        }
      })
      this.loading.dangerTable = true
      getWarningInfo({ deviceId: 0, pageNumber: 1, pageSize: 10 }).then(
        res => {
          if (res.code === 200) {
            this.dangerTableData = res.data.result
            this.loading.dangerTable = false
          }
        }
      )
    },
    changeChart (e) {
      changeChartType({ deviceId: 0, sceneType: e }).then(res => {
        if (res.code === 200) {
          console.log('保存成功')
        }
      })
      this.chartId = e
      this.getChartData()
    },
    getPlatformStatistics () {
      getPlatformStatistics().then(res => {
        if (res.code === 200) {
          let info = res.data.data
          for (let item of this.deviceList) {
            item.value = info[item.type]
          }
        }
      })
    },
    getChartType () {
      getChartType({ id: 0 }).then(res => {
        if (res.code === 200) {
          if (res.data.sceneType) {
            this.chartId = res.data.sceneType
          } else {
            this.chartId = 'POWER_GENERATION_AMOUNT'
          }
          this.getChartData()
        }
      })
    },
    getName (id) {
      for (let item of this.chartOptions) {
        if (id === item.id) {
          return item.name
        }
      }
    },
    ChangeShow (type) {
      if (type === 1) {
        this.showTitle = false
      } else {
        setTimeout(() => {
          this.showTitle = true
        }, 2000)
      }
    },
    getChartData () {
      let { deviceId, chartId: businessType } = this
      getChartSingle({ deviceId, businessType }).then(res => {
        if (res.code === 200) {
          let data = res.data.indexs[0]
          let xData = data.value.map(i => {
            return i.x
          })
          let legend = data.legend.map(i => {
            return i.name
          })
          let yDatas = []
          for (let item of data.legend) {
            let array = []
            for (let datas of data.value) {
              array.push(datas[item.key])
            }
            yDatas.push(array)
          }
          let series = []
          for (let idx = 0; idx <= yDatas.length - 1; idx++) {
            let colors = this.getColors(idx)
            series.push({
              name: legend[idx],
              data: yDatas[idx],
              type: 'line',
              smooth: true,
              itemStyle: {
                color: colors.color
              },
              lineStyle: {
                color: colors.color,
                shadowBlur: 6,
                shadowColor: colors.shadowColor,
                shadowOffsetY: 3
              },
              emphasis: {
                label: {
                  show: true,
                  position: 'outside'
                }
              }
            })
          }
          let info = { series, xData, legend }
          this.initChart(info)
        } else {
          let info = { series: [], xData: [], legend: [] }
          this.initChart(info)
        }
      })
    },
    getColors (index) {
      switch (index) {
        case 0:
          return { color: '#1CC96C', shadowColor: 'rgba(255, 255, 255, 0.6)' }
        case 1:
          return { color: '#FFCE08', shadowColor: 'rgba(184, 193, 29, 1)' }
        case 2:
          return { color: '#0084FF', shadowColor: 'rgba(0, 0, 0, 0.16)' }
        case 3:
          return { color: '#ffb980', shadowColor: 'rgba(0, 0, 0, 0.66)' }
      }
    },
    initChart (info) {
      console.log('初始化')
      let chart = document.getElementById('chart')
      this.$echarts.dispose(chart)
      let line = this.$echarts.init(chart)
      line.setOption({
        xAxis: {
          type: 'category',
          data: info.xData,
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.6)'
            }
          },
          axisLabel: {
            color: 'rgba(255, 255, 255, 0.6)'
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          type: 'value',
          scale: true,
          splitNumber: 5,
          axisLine: {
            lineStyle: {
              color: 'rgba(255, 255, 255, 0.6)'
            }
          },
          axisLabel: {
            color: 'rgba(255, 255, 255, 0.6)'
          },
          axisTick: {
            show: false
          },
          splitLine: {
            lineStyle: {
              type: 'dashed',
              color: 'rgba(255, 255, 255, 0.6)'
            }
          }
        },
        grid: {
          bottom: 20,
          left: 60,
          right: 0
        },
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        legend: {
          data: info.legend,
          icon: 'circle',
          itemWidth: 7,
          itemHeight: 7,
          right: 0,
          textStyle: {
            color: '#cccc'
          }
        },
        series: info.series
      })
    },
    changeModal () {
      setTimeout(() => {
        this.showModal = false
      }, 2000)
    },
    initMap () {
      this.map = new AMap.Map('map', {
        zoom: 14, // 级别
        center: this.location // 中心点坐标
      })
      this.map.setMapStyle('amap://styles/2004f260216ebbad520cb0639fd2d51f')
      let mapInfo = new AMap.InfoWindow({
        offset: new AMap.Pixel(8, -20),
        isCustom: true,
        anchor: 'bottom-left'
      })
      this.changeModal()
      this.getAddress(this.map, mapInfo)
    },
    async genMarker (map, mapInfo) {
      // let list = []
      // console.log('--------------', this.markerList)
      // await this.getAddress()
      // this.markerList.forEach((v) => {
      //   let marker = new AMap.Marker({
      //     position: new AMap.LngLat(v[0], v[1])
      //   })
      //   list.push(marker)
      //   marker.on('click', (e) => {
      //     console.log(e)
      //     // map.setZoomAndCenter(13, v)
      //     this.$router.push({ name: 'single-device-group', params: { id: '1100' } })
      //   })
      //   // marker.on('mouseover', (e) => {
      //   //   console.log('---------------', this.addressList)
      //   //     mapInfo.setContent(`<div style="background-color: #262C3A;padding: 12px;border-radius: 8px;box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16)">
      //   //     <div style="color: rgba(255,255,255,.6);margin-bottom: 8px">机组位置：<span style="color:#fff">潍坊市高新技术产业开发区福寿东街197</span> </div>
      //   //     <div style="color: rgba(255,255,255,.6);margin-bottom: 8px">机组编号：<span style="color:#fff">SH-003</span> </div>
      //   //     <div style="color: rgba(255,255,255,.6);margin-bottom: 8px">负责人：<span style="color:#fff">张三</span> </div>
      //   //     <div style="color: rgba(255,255,255,.6);margin-bottom: 8px">联系方式：<span style="color:#fff">13032782234</span> </div>
      //   //   </div>`)
      //   //   mapInfo.open(map, v)
      //   // })
      //   // marker.on('mouseout', (e) => {
      //   //   mapInfo.close()
      //   // })
      // })
      // map.add(list)
    },
    getAddress (map, mapInfo) {
      getAddress().then(res => {
        if (res.code === 200) {
          let array = []
          let list = []
          let infos = res.data.data
          for (let item of infos) {
            if (item.latitude && item.longitude) {
              array.push([item.longitude, item.latitude, item.deviceId])
            }
          }
          this.markerList = array
          array.forEach(v => {
            let marker = new AMap.Marker({
              position: new AMap.LngLat(v[0], v[1]),
              extData: v[2]
            })
            list.push(marker)
            marker.on('click', e => {
              console.log(e.target.getExtData())
              this.$router.push({
                name: 'single-device-group',
                params: { id: e.target.getExtData() }
              })
            })
          })
          map.add(list)
        } else {
          this.markerList = [[119.209927, 36.725914]]
        }
      })
    },
    handleSubmit () {
      console.log('查询')
    },
    jump () {
      this.map.setCenter([117, 40])
    },
    fullSreen () {
      this.isFull = !this.isFull
    },
    handleClick (row) {
      console.log(row)
    }
  }
}
</script>

<style lang="less" scoped>
.single-device {
  display: flex;
  width: 100%;
  height: 100%;
  .border {
    border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  }
  .left {
    width: 465px;
    padding: 0 24px;
    // background-color: #fff;
    // border-right: 1px solid rgba(0, 0, 0, 0.08);
    overflow: scroll;
    &-head {
      margin-top: 24px;
      padding-bottom: 12px;
      font-size: 16px;
      // color: rgba(0, 0, 0, 1);
      color: #fff;
    }
    &-device {
      display: flex;
      flex-wrap: wrap;
      // background-color: rgba(240, 243, 248, 1);
      background-color: #000000;
      border-radius: 8px;
      padding: 12px;
      width: 417px;
      .form {
        width: 100%;
        display: flex;
        align-items: flex-end;
        margin-bottom: 12px;
        &-item-select {
          width: 160px;
          margin-right: 12px;
          font-size: 14px;
          color: #000000;
          .label {
            margin-bottom: 12px;
          }
          .select {
            height: 28px;
          }
        }
        &-item-button {
          flex: 1;
          margin-left: 12px;
          /deep/ .ivu-btn {
            width: 60px;
            height: 28px;
            background-color: #0084ff;
          }
        }
      }

      .item {
        width: 190px;
        height: 82px;
        display: flex;
        // align-items: center;
        margin-right: 12px;
        margin-bottom: 13px;
        border-radius: 8px;
        // background-color: #fff;
        background-color: #262c3a;
        // box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.04);
        box-shadow: 0px 3px 6px rgba(33, 34, 39, 0.04);
        &:nth-child(2n) {
          margin-right: 0;
        }
        &:last-child {
          margin-bottom: 0;
        }
        &-right {
          flex: 2;
          margin-top: 13px;
          // color:rgba(0, 0, 0, .6);
          color: rgba(255, 255, 255, 0.6);
          font-size: 14px;
          .data {
            margin-top: 8px;
            span {
              &:first-child {
                font-size: 18px;
                font-family: Alibaba PuHuiTi;
                // font-weight: 700;
                color: #fff;
              }
            }
          }
        }
        &-left {
          width: 40px;
          height: 40px;
          margin-top: 21px;
          margin-right: 12px;
          margin-left: 24px;
          border-radius: 12px;
          text-align: center;
          line-height: 40px;
          .iconfont {
            font-size: 20px;
          }
        }
      }
    }
    &-enivronment,
    &-danger {
      padding: 24px 0;
      font-size: 14px;
      // color: #000000;
      color: #fff;
    }
    &-enivronment {
      .chart {
        margin-top: 12px;
      }
    }
    &-danger {
      .title {
        margin-bottom: 12px;
      }
    }
  }

  .right {
    width: 1199px;
    height: 100%;
    padding: 0 23px;
    overflow: auto;
    &-monitor {
      width: 100%;
      .right-head {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        height: 46px;
        // color: #000000;
        color: #fff;
        font-size: 16px;
        &-right {
          display: flex;
          align-items: center;
          .select,
          .jump {
            margin-right: 12px;
          }
          .select {
            width: 160px;
            height: 28px;
            font-size: 14px;
          }
          .jump,
          .full-screen {
            .ivu-btn {
              width: 80px;
              height: 28px;
              background-color: #000;
              color: rgba(255, 255, 255, 0.6);
              border: none;
            }
          }
          .jump {
            .ivu-btn {
              background-color: #0084ff;
              border: none;
              color: #fff;
            }
          }
        }
      }
      .right-screen {
        width: 100%;
        height: 627px;
        margin-top: 12px;
        margin-bottom: 24px;
        border-radius: 8px;
        background-color: #f0f3f8;
        position: relative;
        .modal {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          background-color: #262c3a;
          z-index: 999;
          font-size: 18px;
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      &.active {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0 24px;
        background-color: #262c3a;
        //  background-color: #fff;
        z-index: 999;
        .right-head {
          .ivu-btn {
            padding: 0 50px;
          }
        }
        .right-screen {
          width: 100%;
          height: calc(100% - 10px);
        }
      }
    }
    &-target {
      display: flex;
      align-items: center;
      margin-bottom: 11px;
      &-item {
        flex: 1;
        margin-right: 12px;
        .label {
          font-size: 14px;
          // color: #000000;
          color: #fff;
          margin-bottom: 12px;
        }
        .content {
          display: grid;
          gap: 8px;
          height: 180px;
          padding: 12px;
          border-radius: 8px;
          // background-color: #f0f3f8;
          background-color: #000;
          &-item {
            display: flex;
            border-radius: 8px;
            // background-color: #fff;
            background-color: #262c3a;
            font-size: 14px;
            text-align: center;
            // box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.04);
            box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.04);
            .value {
              text-align: left;
              .num {
                // color: #000000;
                color: #fff;
                // font-weight: 700;
                font-size: 18px;
              }
              .symbol {
                font-size: 12px;
              }
            }
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
      &-financial {
        .content {
          grid-template-columns: repeat(3, 1fr);
          grid-template-rows: repeat(2, 1fr);
          &-item {
            flex-direction: column;
            justify-content: center;
            align-items: center;
          }
        }
      }
      &-performence,
      &-reliable {
        .content {
          grid-template-columns: repeat(2, 1fr);
          grid-template-rows: repeat(2, 1fr);
          &-item {
            align-items: center;
            .icon {
              width: 40px;
              height: 40px;
              margin: 0 12px 0 10px;
              border-radius: 12px;
              line-height: 40px;
              text-align: center;
              .iconfont {
                font-size: 24px;
              }
            }
          }
        }
      }
    }
  }
}
</style>



